  color: #fff;
  cursor: pointer;
  background-color: #2060f6;
  border: none;
  border-radius: 8px;
  width: 100%;
  max-width: 300px;
  height: 50px;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 22.5px;
  transition: background-color .3s;
}

@media (width >= 640px) {
  .button {
    max-width: 264px;
  }
}

.button:hover {
  background-color: #144ed4;
}
